<template>
    <div>
        <div class="btn-group" role="group" aria-label="Button group with nested dropdown">
        <button type="button" class="btn" @click="triggerPage(index)" v-for="(value, index) in page_index_list" :key="index" :class="{'btn-secondary': index!=current_page_index, 'btn-primary': index==current_page_index}">{{value}}</button>

        <div class="btn-group" role="group">
            <button id="btnGroupDrop1" type="button" class="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            数据集
            </button>
            <div class="dropdown-menu" aria-labelledby="btnGroupDrop1">
            <a class="dropdown-item" href="#" @click="triggerDataset(dataset_name)" v-for="(dataset_name, dataset_index) in dataset" :key="dataset_index">{{dataset_name}} <span class="badge badge-light">{{sample_num_in_dataset[dataset_name]}}</span></a>
            </div>
        </div>

        </div>
        <div class="data-and-tag-layout">
            <div v-for="(group, group_index) in page_content" :key="group_index">
                <div class="data-and-tag-card">
                    <div style="align-self:flex-end; margin-right:20px">
                        <span class="badge badge-info">{{current_dataset}}</span>
                    </div>
                    <div class="data-and-tag-group">
                        <div class="data-and-tag-cell" v-for="(cell, index) in group" :key="index">
                            <div class="data-item">
                                <img v-if="cell.type == 'IMAGE' " :src="cell.data" :style="cell.style"/>
                                <a v-else-if="cell.type == 'FILE'" >{{cell.title}}</a>
                                <p v-else>{{cell.data}}</p>
                            </div>
                            <div class="tag-item">
                                <div v-for="(att_value, att_key, att_index) in cell['attribute']" :key="att_index" class="tag-attr">
                                    <label>{{att_key}}</label>
                                    <p>{{att_value}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ExperimentResults', 
    data(){
        return {
            dataset: [],
            sample_num_in_dataset: {},
            page_index_list: [],
            page_content: [],
            current_page_index: 0,
            current_dataset: '',
        }
    },
    mounted: function(){
        var _this = this;
         this.$axios.get(this.BASE_URL+'/batch-api/entry/', {
             params:{

             }
         }).then(function(res){
                // response_content = {
                //     'num_in_page': num_in_page,
                //     'page_num': page_num,
                //     'page_index': page_index,
                //     'dataset_name': dataset_names,
                //     'result_num': result_num,
                //     'result_list': dataset_and_results[start_index: end_index+1]
                // }

                _this.dataset = res.data.content['dataset_name'];
                _this.page_index_list = []
                for(var i=0; i<res.data.content['page_num']; ++i){
                    _this.page_index_list.push(i);
                }
                _this.page_content = res.data.content['result_list'];
                _this.current_page_index = 0;
                _this.sample_num_in_dataset = res.data.content['sample_num_in_dataset']
                _this.current_dataset = res.data.content['current_dataset_name'];
                console.log( _this.current_dataset)
         })
    },
    methods:{
        triggerPage: function(p){
            var _this = this;
            this.$axios.get(this.BASE_URL+'/batch-api/page/', {
                params:{
                    dataset_name: _this.current_dataset,
                    page_index: p
                }
            }).then(function(res){
                    // response_content = {
                    //     'num_in_page': num_in_page,
                    //     'page_num': page_num,
                    //     'page_index': page_index,
                    //     'dataset_name': dataset_names,
                    //     'result_num': result_num,
                    //     'result_list': dataset_and_results[start_index: end_index+1]
                    // }
                    _this.page_content = res.data.content['result_list'];
                    _this.current_page_index = p;
                    console.log('in page '+p)
            })
        },
        triggerDataset: function(d){
            var _this = this;
            this.$axios.get(this.BASE_URL+'/batch-api/entry/', {
                params:{
                    dataset_name: d
                }
            }).then(function(res){
                    // response_content = {
                    //     'num_in_page': num_in_page,
                    //     'page_num': page_num,
                    //     'page_index': page_index,
                    //     'dataset_name': dataset_names,
                    //     'result_num': result_num,
                    //     'result_list': dataset_and_results[start_index: end_index+1]
                    // }

                    _this.dataset = res.data.content['dataset_name'];
                    _this.page_index_list = []
                    for(var i=0; i<res.data.content['page_num']; ++i){
                        _this.page_index_list.push(i);
                    }
                    _this.page_content = res.data.content['result_list'];
                    _this.current_page_index = 0;
                    _this.sample_num_in_dataset = res.data.content['sample_num_in_dataset']
                    _this.current_dataset = res.data.content['current_dataset_name'];
            })
        }
    }
}
</script>

<style scoped>
.data-gallery-grid{
    display: flex;
    flex-direction: column;
}
.operator-layout{
}
.data-and-tag-layout{
    display: flex;
    flex-direction: column;
}
.data-and-tag-cell{
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}
.data-item{

}
.tag-item{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.data-and-tag-group{
    display: flex;
    justify-content: center;
}
.data-and-tag-card{
    border: 1px solid #9999;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    padding-top: 10px;
    flex-direction: column;
}
/* .data-and-tag-card:nth-child(2){
    align-self: center;
} */
.tag-attr{
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
}
</style>


